import { useState } from 'react';
import PropTypes from 'prop-types';
import { useSnackbar } from 'notistack';

import Box from '@mui/material/Box';
import MenuItem from '@mui/material/MenuItem';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import IconButton from '@mui/material/IconButton';
import { Stack, TextField, ListItemText, InputAdornment } from '@mui/material';

import { fDate } from 'src/utils/format-time';
import axiosInstance, { endpoints } from 'src/utils/axios';

import Iconify from 'src/components/iconify';
import CustomPopover, { usePopover } from 'src/components/custom-popover';

// ----------------------------------------------------------------------

export default function TableDetailsRow({ row, index, selected, onEditRow, showAll, onSelectRow }) {
  const {
    code,
    unit_service_type,
    country,
    city,
    email,
    insurance,
    info,
    sector,
    commercial_name,
    province,
    address,
    phone_number_1,
    Phone_number_2,
    work_shift,
    constitution_objective,
    type_of_specialty_1,
    type_of_specialty_2,
    status,
    com_note,
    created_at,
    user_creation,
    ip_address_user_creation,
    updated_at,
    user_modification,
    ip_address_user_modification,
    modifications_nums,
  } = row;

  const { enqueueSnackbar } = useSnackbar();
  const [text, setText] = useState(com_note);
  const popover = usePopover();
  const DDL = usePopover();

  const handleChangeStatus = async (event) => {
    try {
      await axiosInstance.patch(endpoints.companies.one(row?._id), { status: event.target.value });
      enqueueSnackbar('done');
    } catch (e) {
      enqueueSnackbar('error', { variant: 'error' });
    }
  };
  const handleSubmitText = async () => {
    try {
      await axiosInstance.patch(endpoints.companies.one(row?._id), { com_note: text });
      enqueueSnackbar('done');
    } catch (e) {
      enqueueSnackbar('error', { variant: 'error' });
    }
  };

  const renderPrimary = (
    <TableRow
      hover
      index={index}
      sx={{ backgroundColor: index % 2 ? 'background.lightgray' : '' }}
      selected={selected}
    >
      <TableCell align="center">
        <Box>{code}</Box>
      </TableCell>
      <TableCell align="center">{unit_service_type}</TableCell>
      <TableCell align="center">{country}</TableCell>
      <TableCell align="center">{city}</TableCell>
      <TableCell align="center">{email}</TableCell>
      <TableCell align="center">{sector}</TableCell>
      <TableCell align="center">{commercial_name}</TableCell>
      <TableCell align="center">{province}</TableCell>
      <TableCell align="center">{address}</TableCell>
      <TableCell align="center">
        <a href={`tel:${phone_number_1}`}>{phone_number_1}</a>
      </TableCell>
      <TableCell align="center">
        <a href={`tel:${Phone_number_2}`}>{Phone_number_2}</a>
      </TableCell>
      <TableCell align="center">
        <Stack direction="row" justifyContent="space-between">
          <a
            rel="noreferrer"
            target="_blank"
            style={{ color: 'green' }}
            href={`tel:${phone_number_1}`}
          >
            <Iconify icon="material-symbols:call" />
          </a>
          <a
            rel="noreferrer"
            target="_blank"
            style={{ color: 'green' }}
            href={`sms:${phone_number_1}`}
          >
            <Iconify icon="solar:chat-round-dots-bold" />
          </a>
          <a
            rel="noreferrer"
            target="_blank"
            style={{ color: 'green' }}
            href={`https://wa.me/${phone_number_1}`}
          >
            <Iconify icon="flowbite:whatsapp-solid" />
          </a>
        </Stack>
      </TableCell>
      <TableCell align="center">
        <TextField select fullWidth value={status} onChange={handleChangeStatus}>
          <MenuItem value="not contact">لم يتم التواصل</MenuItem>
          <MenuItem value="agreed">قبول</MenuItem>
          <MenuItem value="refused">رفض</MenuItem>
          <MenuItem value="no number">لا يوجد رقم للتواصل</MenuItem>
          <MenuItem value="wrong number">الرقم خاطئ</MenuItem>
        </TextField>
      </TableCell>
      <TableCell align="center">
        <TextField
          fullWidth
          multiline
          value={text}
          onChange={(e) => setText(e.target.value)}
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton onClick={handleSubmitText} edge="end">
                  <Iconify icon="icon-park-solid:correct" />
                </IconButton>
              </InputAdornment>
            ),
          }}
        />
      </TableCell>
      {showAll && (
        <>
          <TableCell align="center">{insurance}</TableCell>
          <TableCell align="center">{info}</TableCell>
          <TableCell align="center">{work_shift}</TableCell>
          <TableCell align="center">{constitution_objective}</TableCell>
          <TableCell align="center">{type_of_specialty_1}</TableCell>
          <TableCell align="center">{type_of_specialty_2}</TableCell>
        </>
      )}

      <TableCell align="right" sx={{ px: 1, whiteSpace: 'nowrap' }}>
        <IconButton color={popover.open ? 'inherit' : 'default'} onClick={popover.onOpen}>
          <Iconify icon="eva:more-vertical-fill" />
        </IconButton>
      </TableCell>
    </TableRow>
  );

  return (
    <>
      {renderPrimary}

      <CustomPopover
        open={popover.open}
        onClose={popover.onClose}
        arrow="right-top"
        sx={{ width: 140 }}
      >
        <MenuItem
          lang="ar"
          onClick={() => {
            onEditRow();
            popover.onClose();
          }}
        >
          <Iconify icon="fluent:edit-32-filled" />
          Edit
        </MenuItem>
        <MenuItem lang="ar" onClick={DDL.onOpen}>
          <Iconify icon="carbon:data-quality-definition" />
          DDL
        </MenuItem>
      </CustomPopover>

      <CustomPopover
        open={DDL.open}
        onClose={DDL.onClose}
        arrow="right-top"
        sx={{
          padding: 2,
          fontSize: '14px',
        }}
      >
        <Box sx={{ fontWeight: 600 }}>Creation Time:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>
          <ListItemText
            primary={fDate(created_at, 'dd MMMMMMMM yyyy')}
            secondary={fDate(created_at, 'p')}
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              component: 'span',
              typography: 'caption',
            }}
          />
        </Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>created by:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>{user_creation?.email}</Box>

        <Box sx={{ pt: 1, fontWeight: 600 }}>created by IP:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>{ip_address_user_creation}</Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>Editing Time:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>
          <ListItemText
            primary={fDate(updated_at, 'dd MMMMMMMM yyyy')}
            secondary={fDate(updated_at, 'p')}
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              component: 'span',
              typography: 'caption',
            }}
          />
        </Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>Editor:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>{user_modification?.email}</Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>Editor IP:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray', fontWeight: '400' }}>
          {ip_address_user_modification}
        </Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>Modifications No: {modifications_nums}</Box>
      </CustomPopover>
    </>
  );
}

TableDetailsRow.propTypes = {
  onSelectRow: PropTypes.func,
  onEditRow: PropTypes.func,
  row: PropTypes.object,
  selected: PropTypes.bool,
  showAll: PropTypes.bool,
  index: PropTypes.number,
};
